import { Link } from "react-router-dom";
import employerLogo2 from "../../assets/images/employer-2.png";

//Icons
import IcLocation from '../../assets/images/icons/ic-location.svg';
import { FavoriteButton } from "../elements/FavoriteButton";

function HighPriorityJobs() {
    return (
        <section className="high-priority-jobs">
            <div className="container">
                <div className="section-title text-center">
                    <h4>High Priority Jobs</h4>
                    <p>Hand picked featured jobs from verified authors</p>
                </div>
                <div className="high-priority-jobs-list">
                    <ul>
                        {
                            [...Array(5)].map((item, idx) => {
                                return (
                                    <li key={idx}>
                                        <div className="high-priority-job-wrapper">
                                            <Link className="employer-logo stretched-link" to="/jobDetails">
                                                <img src={employerLogo2} alt="" />
                                            </Link>
                                            <div className="jobs-details">
                                                <span className="job-position">Human Resource</span>
                                                <div className="job-metas">
                                                    <div className="job-employer">Wonka Industries</div>
                                                    <div className="job-location with-icon">Baghdad, Iraq</div>
                                                    <div className="job-type with-icon">Full Time</div>
                                                </div>
                                                <div className="job-deadline">Deadline: June  23, 2022</div>
                                                <FavoriteButton />
                                                <Link className="button button-light-blue" to="/createResume/PersonalInformation">Apply Now</Link>
                                            </div>
                                        </div>
                                    </li>
                                )
                            })
                        }
                    </ul>
                    <div className="text-center">
                        <button className="button button-light-blue">View All Job</button>
                    </div>
                </div>
            </div>
        </section>
    )
}

export { HighPriorityJobs }