import {Link} from "react-router-dom";

// Images
import IcSearch from "../../assets/images/icons/ic-search-white.svg";




function HeroBanner(){
    return (
        <section className="hero-banner">
            <div className="container">
                <div className="banner-content">
                    <div className="banner-title">
                        <h4>Find and become a <br/><span>Professional</span> with Passion</h4>
                        <p>Job search platform worlwide. We connect freelancers and startups in an easy way and good Collaboration!</p>
                    </div>

                    <div className="jobs-profile-suggestions">
                        <ul>
                            <li>
                                <Link to="/">Front-end</Link>
                            </li>
                            <li>
                                <Link to="/">UI Designer</Link>
                            </li>
                            <li>
                                <Link to="/">3d-Illustration</Link>
                            </li>
                            <li>
                                <Link to="/">Product Manager</Link>
                            </li>
                            <li>
                                <Link to="/">UX Designer</Link>
                            </li>
                            <li>
                                <Link to="/">Marketing</Link>
                            </li>
                            <li>
                                <Link to="/">Back-end</Link>
                            </li>
                            <li>
                                <Link to="/">SEO Manager</Link>
                            </li>
                            <li>
                                <Link to="/">Marketing Head</Link>
                            </li>
                        </ul>
                    </div>


                    <div className="search-jobs-form">
                        <div className="job-title job-input">
                            <input className="form-control" type="text" placeholder="Job Title"/>
                        </div>
                        <div className="job-location job-input">
                            <input className="form-control" type="text" placeholder="Location"/>
                        </div>
                        <Link className="button button-blue" to="/joblist"> <img src={IcSearch} alt="" /> Search</Link>
                    </div>
                </div>
            </div>
        </section>
    )
}

export {HeroBanner};