

import { Link } from "react-router-dom";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
// import "slick-carousel/slick/slick-theme.css";

import employerLogo1 from "../../assets/images/employer-1.png";

function JobOffers() {

    const sliderSettings = {
        className: "center",
        centerMode: true,
        infinite: true,
        centerPadding: "160px",
        slidesToShow: 4,
        slidesToScroll: 1,
        speed: 500,
        arrows: true,
        dots: false,
        responsive: [
            {
              breakpoint: 1600.99,
              settings: {
                slidesToShow: 3,
                infinite: true,
              }
            },
            {
                breakpoint: 1299.99,
                settings: {
                  slidesToShow: 2,
                  infinite: true,
                  centerPadding: "200px",
                }
            },
            {
                breakpoint: 1199,
                settings: {
                  slidesToShow: 2,
                  infinite: true,
                  centerPadding: "100px",
                }
            },
            {
                breakpoint: 991,
                settings: {
                  slidesToShow: 1,
                  infinite: true,
                  centerPadding: "100px",
                  arrows:false
                }
            },
            {
                breakpoint: 575,
                settings: {
                  slidesToShow: 1,
                  infinite: true,
                  centerPadding: "20px",
                  arrows:false
                }
            }
          ]
      };

    return (
        <section className="job-offers">
            <div className="container">
                <div className="section-title text-center">
                    <h4>Best Job Offer For You</h4>
                    <p>Discover jobs most relevant to you by Experience level, Salary, location, Job type, etc.</p>
                </div>
            </div>
            <div className="jobs-offer-slider">
            <Slider {...sliderSettings} >
                {
                    [...Array(5)].map((item, idx) => {

                        return (
                            <div className="item">
                                <div className="job-box">
                                    <Link className="employer-logo stretched-link" to="/jobDetails">
                                        <img src={employerLogo1} alt="" />
                                    </Link>
                                    <div className="job-types">
                                        <Link to="/" className="type-job">Full Time</Link>
                                        <Link to="/" className="type-job">Design</Link>
                                    </div>
                                    <div className="job-title">
                                        <h4>Junior Ui/Ux Designer</h4>
                                        <p>Wonka Industries</p>
                                    </div>
                                    <div className="job-metas">
                                        <span className="job-location with-icon">Baghdad, Iraq</span>
                                    </div>
                                    <div className="d-sm-flex justify-content-sm-between align-items-sm-center">
                                        <span className="job-salary">$24-38K</span>
                                        <Link className="button button-blue" to="/createResume/PersonalInformation">Apply Now</Link>
                                    </div>
                                </div>
                            </div>
                        )

                    })
                }
            </Slider>
            </div>
            

        </section>
    )
}


export { JobOffers };