import { Link } from "react-router-dom";
import androidStoreImg from "../../assets/images/icons/android-store.png"
import iosStoreImg from "../../assets/images/icons/ios-store.png"
import mobileImg from "../../assets/images/get-app-media.png"
function GetApp() {
    return (
        <section className="get-app">
            <div className="container">
                <div className="row align-items-lg-center flex-lg-row-reverse">
                    <div className="col-lg-6">
                        <div className="get-app-media text-lg-end text-center">
                            <img src={mobileImg} alt="" />
                        </div>
                    </div>
                    <div className="col-lg-6">
                        <div className="section-title text-lg-start text-center">
                            <h4>Get App Now!</h4>
                            <p className="ms-lg-0">Lorem ipsum dolor sit amet, consectetur adipiscing elit diam fringilla arcu tristique rutrum. Vel orci rhoncus ut sagittis. </p>
                        </div>
                        <div className="app-download-links text-center text-lg-start">
                            <Link to="/"><img src={androidStoreImg} alt="" /></Link>
                            <Link to="/"><img src={iosStoreImg} alt="" /></Link>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export { GetApp }